import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { BcAvatarBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

@Component({
  selector: 'bc-avatar',
  templateUrl: 'bc-avatar.component.html',
})
export class BcAvatarComponent implements OnInit {
  @Input() size: string;
  @Input() id: string;
  @Input() icon: string;
  @Input() illustration: string;
  @Input() alt: string;
  @Input() focus: string;
  @Input() ariaLabel: string;
  @Input() tooltipText: string;

  bcAvatarBehavior: BcAvatarBehavior;

  useTooltip: boolean = false;

  constructor(
    private bcHelperService: BcHelperService,
    private el: ElementRef
  ) {}

  ngOnInit(): void {
    this.id = this.id ?? this.bcHelperService.getId(this);
    if (this.tooltipText) {
      this.useTooltip = true;
    }
  }

  ngAfterViewInit(): void {
    this.initAvatarBehavior();
    if (this.ariaLabel) {
      this.el.nativeElement
        .querySelector('div')
        .setAttribute('aria-label', this.ariaLabel);
    }
  }

  initAvatarBehavior(): void {
    this.bcAvatarBehavior = new BcAvatarBehavior();
    this.bcAvatarBehavior.setId(this.id);
    this.bcAvatarBehavior.setUp();
  }
}
